/* =====================================================================================
Basic styles */
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Raleway", "Helvetica", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: #ffffff; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

* {
  box-sizing: border-box; }

/* =====================================================================================
Container */
.container {
  max-width: 1366px;
  width: 100%;
  margin: 0 auto;
  padding-top: 0;
  padding-right: 32px;
  padding-bottom: 0;
  padding-left: 32px; }

@media (max-width: 1023px) {
  .container {
    max-width: 1023px;
    width: 100%;
    margin: 0 auto;
    padding-top: 0;
    padding-right: 16px;
    padding-bottom: 0;
    padding-left: 16px; } }

@media (max-width: 767px) {
  .container {
    max-width: 767px;
    width: 100%;
    margin: 0 auto;
    padding-top: 0;
    padding-right: 16px;
    padding-bottom: 0;
    padding-left: 16px; } }

/* =====================================================================================
Visually-hidden */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* =====================================================================================
Btn-page */
.btn-page {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 65px;
  max-width: 250px;
  width: 100%;
  font-weight: 800;
  font-size: 17px;
  line-height: 20px;
  text-transform: uppercase;
  color: #ffffff;
  border-radius: 65px;
  background-color: #f2890f;
  transition: all 0.1s linear; }

.btn-page:hover,
.btn-page:focus {
  background-color: #f39425; }

.btn-page:active {
  background-color: rgba(243, 148, 37, 0.7); }

/* =====================================================================================
Header style */
.header {
  position: fixed;
  top: 0;
  width: 100%;
  padding-top: 24px; }

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header__link:hover .header__logo-img,
.header__link:focus .header__logo-img {
  opacity: 0.8; }

.header__link:active .header__logo-img {
  opacity: 0.6; }

.header__burger {
  display: none;
  position: relative;
  padding-right: 27px;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  color: #f0f0f0; }

.header__burger::after {
  position: absolute;
  right: 0;
  bottom: 0;
  content: "";
  height: 15px;
  width: 15px;
  background-image: url(../img/burger-close.svg);
  background-repeat: no-repeat; }

.header__contacts {
  display: flex;
  align-items: center; }

.header__contacts-city {
  position: relative;
  display: flex;
  align-items: center;
  display: inline-block;
  margin-right: 24px;
  padding-left: 24px;
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
  color: #f2890f;
  transition: all 0.1s linear; }

.header__contacts-text {
  transition: all 0.1s linear; }

.header__contacts-city:hover .header__contacts-text,
.header__contacts-city:focus .header__contacts-text {
  border-bottom: 1px dashed #f2890f; }

.header__contacts-city:active .header__contacts-text {
  border-bottom-style: solid; }

.header__contacts-city::after {
  position: absolute;
  top: -3px;
  left: 0;
  content: "";
  display: block;
  width: 16px;
  height: 21px;
  background-image: url("../img/icon-location.svg");
  background-repeat: no-repeat; }

.header__contacts-tel {
  margin-top: 0;
  margin-right: 7px;
  margin-bottom: 0;
  font-weight: 600;
  line-height: 16px;
  color: #F0F0F0; }

/* =====================================================================================
Media max-width: 1023px*/
@media (max-width: 1023px) {
  .header__contacts-city {
    margin-right: 12px;
    font-size: 11px;
    line-height: 13px; }
  .header__logo-img {
    height: 36px;
    width: 96px; }
  .header__contacts-tel {
    font-size: 12px;
    line-height: 14px; } }

/* =====================================================================================
Media max-width: 767px*/
@media (max-width: 767px) {
  .header__inner {
    flex-wrap: wrap; }
  .header__link {
    flex-basis: 50%; }
  .header__burger {
    display: block;
    flex-basis: 50%;
    text-align: right; }
  .header__contacts {
    flex-direction: column;
    flex-basis: 100%;
    padding-top: 44px;
    padding-bottom: 40px;
    text-align: center; }
  .header__contacts-city {
    max-width: 100%;
    margin-right: 0;
    margin-bottom: 22px;
    font-weight: bold;
    font-size: 12px;
    line-height: 14px; }
  .header__contacts-city::after {
    top: -3px; }
  .header__contacts-tel {
    font-size: 14px;
    line-height: 16px; } }

.main-nav {
  max-width: 531px;
  width: 100%; }

.main-nav__list {
  display: flex;
  justify-content: space-between;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none; }

.main-nav__list-link {
  display: inline-block;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: #f0f0f0; }

.main-nav__list-link:hover,
.main-nav__list-link:focus {
  color: rgba(240, 240, 240, 0.8); }

.main-nav__list-link:active {
  color: #f2890f; }

/* =====================================================================================
Media max-width: 1023px*/
@media (max-width: 1023px) {
  .main-nav {
    max-width: 380px; }
  .main-nav__list-link {
    font-size: 12px;
    line-height: 12px; } }

/* =====================================================================================
Media max-width: 767px*/
@media (max-width: 767px) {
  .main-nav {
    max-width: 100%;
    order: 1; }
  .main-nav__list {
    flex-direction: column;
    text-align: center; }
  .main-nav__list-link {
    margin-bottom: 40px;
    font-weight: 600;
    font-size: 16px;
    line-height: 19px; } }

/* =====================================================================================
Index.html style */
.intro {
  height: 100vh;
  background-image: url("../img/intro-bg-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover; }

.intro__inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 104px;
  height: 100vh; }

.intro__title {
  max-width: 585px;
  margin-top: 0;
  margin-bottom: 48px;
  font-weight: 800;
  font-size: 64px;
  line-height: 70px;
  color: #ffffff; }

.intro__text {
  max-width: 468px;
  margin-top: 0;
  margin-bottom: 64px;
  font-weight: 500;
  font-size: 17px;
  line-height: 23px;
  color: #E6E6E6; }

/* =====================================================================================
Media max-width: 767px*/
@media (max-width: 767px) {
  .intro {
    background-image: url("../img/intro-bg-mobile.jpg"); }
  .intro__inner {
    justify-content: flex-end;
    padding-left: 0; }
  .intro__title {
    max-width: 336px;
    margin-right: auto;
    margin-bottom: 32px;
    margin-left: auto;
    font-size: 36px;
    line-height: 42px;
    text-align: center; }
  .intro__text {
    display: none; }
  .intro__btn {
    margin-right: auto;
    margin-bottom: 64px;
    margin-left: auto; } }

/* =====================================================================================
Footer style */
.footer {
  position: fixed;
  bottom: 0;
  padding-bottom: 24px;
  width: 100%; }

.footer__inner {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.footer__inner__list {
  display: flex;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none; }

.footer__list-item {
  margin-right: 7px; }

.footer__list-link {
  display: block;
  height: 28px;
  width: 28px; }

.footer__list-link--insta {
  background-image: url(../img/instagram-default.png);
  transition: all 0.1s linear; }

.footer__list-link--insta:hover,
.footer__list-link--insta:focus {
  background-image: url(../img/instagram-hover.png); }

.footer__list-link--insta:active {
  opacity: 0.5; }

.footer__list-link--vk {
  background-image: url(../img/vk-default.png);
  transition: all 0.1s linear; }

.footer__list-link--vk:hover,
.footer__list-link--vk:focus {
  background-image: url(../img/vk-hover.png); }

.footer__list-link--vk:active {
  opacity: 0.5; }

.footer__text-question {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 500;
  font-size: 12px;
  color: #e6e6e6;
  border-bottom: 1px dashed #e6e6e6;
  transition: all 0.1s linear; }

.footer__text-question:hover,
.footer__text-question:focus {
  color: #f2890f;
  border-bottom-color: #f2890f; }

.footer__text-question:active {
  border-bottom-style: solid; }

.footer__text-question span {
  display: none;
  transition: all 0.1s linear; }

.footer__text-question:hover span,
.footer__text-question:focus span {
  display: inline-block; }

/* =====================================================================================
Media max-width: 767px*/
@media (max-width: 767px) {
  footer {
    display: none; } }

/* =====================================================================================
Your-city style */
.your-city {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1000;
  max-width: 480px;
  width: 100%;
  padding-top: 32px;
  padding-left: 32px;
  padding-bottom: 33px;
  background: linear-gradient(0deg, #141414 0%, #1F1D1D 100%);
  border-radius: 3px;
  transform: translate(-50%, -50%);
  opacity: 0;
  visibility: hidden;
  pointer-events: none; }

.your-city--show {
  opacity: 1;
  visibility: visible;
  pointer-events: auto; }

.your-city__btn-close {
  position: absolute;
  top: 39px;
  right: 29px;
  height: 18px;
  width: 18px;
  border-color: transparent;
  background-color: transparent;
  cursor: pointer; }

.your-city__btn-svg {
  position: absolute;
  top: -1px;
  left: -1px;
  stroke: rgba(255, 255, 255, 0.5);
  transition: all 0.1 linear; }

.your-city__btn-close:hover .your-city__btn-svg,
.your-city__btn-close:focus .your-city__btn-svg {
  stroke: rgba(255, 255, 255, 0.8); }

.your-city__btn-close:active .your-city__btn-svg {
  stroke: white; }

.your-city__title {
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: 800;
  font-size: 32px;
  line-height: 32px; }

.your-city__gps-text,
.your-city__text {
  font-weight: normal;
  font-size: 17px;
  line-height: 17px;
  color: #e6e6e6; }

.your-city__gps-text {
  position: relative;
  margin-top: 0;
  margin-bottom: 40px;
  padding-left: 30px; }

.your-city__gps-text::after {
  position: absolute;
  left: 0;
  top: 0;
  content: "";
  height: 20px;
  width: 20px;
  background-image: url("../img/gps-location.svg");
  background-repeat: no-repeat; }

.your-city__text {
  margin-top: 0;
  margin-bottom: 16px; }

.your-city-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none; }

.your-city-list__link {
  display: inline-block;
  margin-bottom: 20px;
  font-weight: bold;
  font-size: 24px;
  line-height: 24px;
  color: rgba(255, 255, 255, 0.32);
  transition: all 0.1s linear; }

.your-city-list__link:hover,
.your-city-list__link:focus {
  color: white; }

.your-city-list__link:active {
  color: rgba(255, 255, 255, 0.7); }

.your-city-list__link--active {
  color: white;
  border-bottom: 2px solid #f2890f; }

/* =====================================================================================
Media max-width: 767px*/
@media (max-width: 767px) {
  .your-city {
    height: 100vh;
    max-width: 100%;
    padding-left: 16px; }
  .your-city__btn-close {
    right: 16px; } }
